
















































































































































































import { Component, Vue } from "vue-property-decorator";
import DataService from "../services/DataService";

@Component
export default class Match extends Vue {
  selectableYears: number[] = [];
  tournamentTableaux: any = [
    {
      rank: null,
      name: null,
      scoredGoals: 0,
      goalDifference: 0,
      points: 0,
    },
  ];
  lastTournamentMatches: any = [
    {
      homeTeam: null,
      awayTeam: null,
      homeGoals: null,
      awayGoals: null,
      date: null,
    }
  ];
  tournament: any = [
    {homeTeam: "Old", awayTeam: "Young", homeGoals: null, awayGoals: null},
    {homeTeam: "Middle", awayTeam: "Young", homeGoals: null, awayGoals: null},
    {homeTeam: "Middle", awayTeam: "Old", homeGoals: null, awayGoals: null},
    {homeTeam: "Young", awayTeam: "Old", homeGoals: null, awayGoals: null},
    {homeTeam: "Young", awayTeam: "Middle", homeGoals: null, awayGoals: null},
    {homeTeam: "Old", awayTeam: "Middle", homeGoals: null, awayGoals: null},
  ];

  async saveTournament() {
    if(confirm("Turnier-Ergebnis eintragen?")) {
      let data = [];
      let i = 0;
      for (const row of this.tournament) {
        data[i] = row;
        data[i].homeGoals = Number.parseInt(row.homeGoals);
        data[i].awayGoals = Number.parseInt(row.awayGoals);
        i++;
      }
      await DataService.saveTournament(data);
      this.updateTournamentTableaux();
      this.updateLastTournamentMatches();
    }
  }


  async updateTournamentTableaux() {
    try {
      this.tournamentTableaux = (await DataService.getTableaux(true, 0, 6)).data;
    } catch (error) {
      console.error(error);
    }
  }

  async updateLastTournamentMatches() {
    try {
      this.lastTournamentMatches = (await DataService.getMatches(true, 6)).data;
    } catch (error) {
      console.error(error);
    }
  }

  mounted() {
    for (let year = new Date().getFullYear()-1; year >= 2017; year--) {
      this.selectableYears.push(year);
    }
    this.updateTournamentTableaux();
    this.updateLastTournamentMatches();
  }
}
